.table-container {
    display: flex;
    /* -webkit-text-decoration: underline var(--highLight);
    text-decoration: underline var(--highLight); */
    width: 69rem;
    text-align: center;
    font-size: 1.5rem;
    margin-inline-start: 19px;
    margin-bottom: 1rem;
    font-family: 'Primeform Pro';
}


.table-text {
    width: 180px;
    text-align: center;
    /* padding-top: 0px px; */
}

.single-ticket {
    display: flex;
    width: 97%;
    justify-content: space-around;
    /* border-bottom: 2px solid var(--gray); */
    font-size: 1rem;
    margin-inline-start: 1rem;
    margin-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-family: 'Roboto Slab', serif;
    color: var(--gray);
}

.ticket-div {
    width: 10.5rem;
    height: 35px;
    text-align: center;
    padding-top: 0rem;
    border: 1px solid var(--lowLight);
    border-left: none;
    border-top: none;
}


.adminSearch {
    outline: 0;
    width: 50%;
    height: 80%;
    border-radius: 5px;
    border: 1px solid rgba(169, 166, 166, 0.52);
    padding: 0.3rem 1rem;
    font-family: "Roboto Slab", serif;
    margin-left: 18rem;
    margin-bottom: 2rem;
}



.loading-spinner-admin {
    width: 40px;
    height: 40px;
    border: 8px solid #f3f3f3;
    border-top: 10px solid var(--offBlack);
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
    margin: 10rem auto;
}